// flex basis
.display-flex {
    display: flex !important; 
}
.d-flex {
    display: flex !important;
    flex-direction: row;
    flex-wrap: wrap;
}
.d-flex-reverse {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: flex-end;
}
